@charset "UTF-8";

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  margin: 0;
  font-size: 16px;
  min-width: 320px;
  line-height: 17px;
  font-family: "Inter", sans-serif;
  color: #0E0E0E;
}

body input,
body textarea {
  border: #666 1px solid;
  outline: none;
}

b,
strong {
  font-weight: 700;
}

::placeholder {
  color: #666;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-style: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 500;
  color: #0E0E0E;
  background-color: #fff;
}

.header {
  display: flex;
  justify-content: center;
}

.intro_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  width: 100%;
}

.container {
  max-width: 1320px;
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
}

.intro {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.intro_menu {
  display: none;
}

.intro_info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.intro_info_title {
  text-align: center;
  font-size: 70px;
  max-width: 650px;
  line-height: 80px;
  padding: 120px 0 60px;
}

.logo_wrap {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.logo_wrap span {
  font-size: 20px;
}

.btn {
  padding: 13px 61px 15px 36px;
  text-decoration: none;
  line-height: 26px;
  position: relative;
  color: #fff;
  background-color: #0E0E0E;
}

.circle {
  position: absolute;
  left: -21%;
  width: 800px;
  height: 800px;
  border-radius: 50%;
  background-color: #f5f5f5;
  z-index: -1;
  top: -52%;
}

.link_circle::after {
  content: "‚óè";
  font-size: 12px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20%;
}

.nav .list_link {
  color: #0E0E0E;
  text-decoration: none;
  position: relative;
}

.nav .list_link::after {
  content: "";
  position: absolute;
  height: 2px;
  background: black;
  width: 0%;
  top: 58%;
  left: -10%;
  transform: translateY(-50%);
  transition: width 0.2s linear;
}

.nav .list_link.active::after {
  width: 120%;
}

.nav .list_item {
  display: inline;
}

.nav .list_item:not(:last-child) {
  margin-right: 55px;
}

.social_links {
  margin-left: -40%;
}

.social_links a:not(:last-child) {
  margin-right: 20px;
}

.social_links .facebook_svg path,
.social_links .twitter_svg path,
.social_links .inst_svg path {
  transition: 200ms;
  transition-timing-function: ease-in-out;
}

.social_links .facebook_svg:hover path {
  fill: #3B5998;
}

.social_links .twitter_svg:hover path {
  fill: #2C6A93;
}

.social_links .inst_svg:hover path {
  fill: #a3094c;
}

.navigation {
  transition: 0.5s;
}

@media only screen and (max-width: 1400px) {
  .container {
    max-width: 1140px;
  }
}

@media only screen and (max-width: 1200px) {
  .container {
    max-width: 960px;
  }

  .container.active {
    padding: 0;
    overflow: hidden;
  }

  .circle {
    left: -40%;
    top: -45%;
  }

  .nav .list_item:not(:last-child) {
    margin-right: 0px;
  }

  .intro_info.active {
    display: none;
  }

  .intro_wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .intro_menu {
    display: flex;
    height: 25px;
    width: 25px;
    position: absolute;
    right: 0;
  }

  .intro_menu span {
    position: absolute;
    width: 25px;
    height: 2px;
    background-color: #0E0E0E;
    top: 50%;
    transition: 0.5s;
  }

  .intro_menu span:nth-of-type(2) {
    top: calc(50% + 4px);
  }

  .intro_menu span:nth-of-type(3) {
    top: calc(50% - 4px);
  }

  .intro_menu.active span:nth-of-type(1) {
    transform: rotate(1turn);
    opacity: 0;
  }

  .intro_menu.active span:nth-of-type(2) {
    top: 50%;
    transform: rotate(45deg);
  }

  .intro_menu.active span:nth-of-type(3) {
    top: 50%;
    transform: rotate(-45deg);
  }

  .navigation {
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: flex-start;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(247, 241, 241);
    transform: translateX(100%);
  }

  .navigation .nav_list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .navigation .nav_list li {
    padding-top: 100px;
  }

  .navigation.active {
    transform: translateX(0%);
  }

  .social_links {
    display: flex;
    justify-content: space-around;
    transition: 0.5s;
    margin: auto;
    z-index: 2;
  }

  .social_links.active {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 50%;
    right: 50%;
  }

  .social_links.active a:nth-of-type(2) {
    margin: 0 216px;
  }

  .social_links.active a:nth-of-type(1) {
    margin: 0 -120px;
  }

  .social_links.active a:nth-of-type(3) {
    margin: 0 -120px;
  }
}

@media only screen and (max-width: 992px) {
  .container {
    max-width: 720px;
  }

  .social_links.active a:nth-of-type(2) {
    margin: 0 216px;
  }

  .social_links.active a:nth-of-type(1) {
    margin: 0 -120px;
  }

  .social_links.active a:nth-of-type(3) {
    margin: 0 -120px;
  }

  .circle {
    left: -75%;
    top: -33%;
  }

  .intro_info_title {
    font-size: 60px;
    max-width: 550px;
    line-height: 60px;
  }
}

@media only screen and (max-width: 768px) {
  .container {
    max-width: 540px;
  }

  .circle {
    left: -121%;
    top: -11%;
  }

  .navigation {
    transform: translateX(150%);
  }

  .navigation .nav ul {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .social_links.active a:nth-of-type(2) {
    margin: 0 216px;
  }

  .social_links.active a:nth-of-type(1) {
    margin: 0 -120px;
  }

  .social_links.active a:nth-of-type(3) {
    margin: 0 -120px;
  }

  .intro_info_title {
    font-size: 50px;
    max-width: 500px;
    line-height: 50px;
  }
}

@media only screen and (max-width: 576px) {
  .circle {
    left: -121%;
    top: -11%;
  }

  .social_links.active a:nth-of-type(2) {
    margin: 0 100px;
  }

  .social_links.active a:nth-of-type(1) {
    margin: 0 -70px;
  }

  .social_links.active a:nth-of-type(3) {
    margin: 0 -70px;
  }

  .intro_info_title {
    font-size: 40px;
    max-width: 400px;
    line-height: 40px;
  }
}